   padding:0; 
   margin: 0px 5px;
}

.ant-menu-horizontal > .ant-menu-item-selected {
background-color: #e6b9ad;
  color: #c091a0
}

.ant-row.ant-form-item{
color: #b1859b;
margin: 15px 2px;

}

.ant-dropdown-link:hover{
  color: #e6caad;
}
