  }
  .ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .div{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .list-group-item.list-group-item{
    width: -webkit-fit-content;
